button {
  width: 250px;
  height: 80px;
  font-size: 1.5rem;
  letter-spacing: 0.1rem;
  font-family:'Roboto', sans-serif;
  font-weight: bold;
  color: whitesmoke;
  border: 2px solid whitesmoke;
  border-radius: 15px;
  margin: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #00471b;
}

.boton-clic {
  background-color: #002ead;
}

.boton-reiniciar {
  background-color: #5a01a7;
}